import React, { useState, useEffect } from "react"
import 'bootstrap/dist/css/bootstrap.css';
import {Image, Container, Row} from 'react-bootstrap'


const API_ENDPOINT = 'https://picsum.photos/v2/list?limit=6&page='

const randomNumber = (maxNumber) =>  Math.floor(Math.random() * maxNumber)

const Test = () => {
    
    const initialState = {
        allNewPhoto: [],
        page: 1
    }

    const [state, setState] = useState(initialState)
    

    const fetchPhoto = () => {
        setState({page:randomNumber(100), allNewPhoto: []})
        fetch(`${API_ENDPOINT}${state.page}`, {
            headers: {
                'Cache-Control': 'no-cache'
            }
        })
            .then(response => response.json())
            .then(response => {
                setState(currState => ({...currState, allNewPhoto: response})) //ni ha .. allnewphoto je aq buat ... so page ilang jadi undefined
                //ni nama dia spread operator ...state means kita nak amik sume dalam state tu
                console.log(response)
            })
            .catch(err => console.error(err))
    }

    useEffect(() => { 
        fetchPhoto()  
        
    }, []);

    const handleClick = () => fetchPhoto() 

    return (
        <>
            <Container>
                
            {state.allNewPhoto.length ? (
               <>                   
                <br/>                
                    <Row>                  
                    
                    {state.allNewPhoto.map((photo, index) => (
                        <div key={index} style={{padding: 10}}> 
                               
                         <Image  src={photo.download_url} width="300px" alt='' rounded  /><br/><br/>
                          
                        </div> 
                    ))}  

                    </Row>                
                </>
            ):'loading'}

            </Container>

            <Container>

            <div className="row h-100">
                <div className="col-sm-12 align-items-center my-auto">
                    <div className="w-25 mx-auto">
                        <img className="center" src="https://image.flaticon.com/icons/svg/1177/1177530.svg" width="50" onClick={() => handleClick()} alt=""/>
                    </div>
                </div>
             </div>

            </Container>
        </>
    )
}


export default Test